.card {
  flex-grow: 1;

  box-shadow: -8px 0 21px 0 transparentize($color-black, 0.928);
}

.card__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 72px);
  overflow-y: scroll;
}

.card__header {
  height: 72px;
  padding: 10px 40px;

  // border-bottom: 1px solid $color-card-line;
}

.card__block-list {
  flex-grow: 1;
  padding: 0 40px;
}

.card__block {
  // border-bottom: 1px solid $color-card-line;
}
